* {
  font-family: Jetbrains Mono, Arial, Helvetica, sans-serif;
}

body {
  padding: 60px;
}

input {
  color: grey;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  max-height: 400px;
  overflow: hidden;
  width: auto;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  object-fit: scale-down;
  height: calc(100% - 4.5rem);
  width: 100%;
}
